div.shell {
    position: absolute;
    right: 20%;
    top: 20%;
    width: 600px;
    height: 12em;
    overflow-y: hidden;
    border: 1px solid black;
    font-family: "Courier New", monospace;
}

shln {
    display: block;
    white-space: pre-wrap;

    --color-sys: yellow;
    --color-host: orange;
    --color-user: blue;
}

shln color-o {
    color: orange;
}

shln color-b {
    color: blue;
}

shln sys {
    color: var(--color-sys);
}

shln user {
    color: var(--color-user);
}

shln host {
    color: var(--color-host);
}

shln exe {

}

shln direc {

}

shln crsr {
    background-color: rgb(253, 148, 8);
    color: black;
    -webkit-animation-timing-function: steps(1, end);
            animation-timing-function: steps(1, end);
    animation-name: cursor_blink;
        -webkit-animation-name: cursor_blink;
    animation-duration: 1.5s;
        -webkit-animation-duration: 1.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@keyframes cursor_blink {
    0% {background-color: rgb(253, 148, 8);}
    50% {background-color: rgb(20, 23, 27);
        color: white;}
    100% {background-color: rgb(253, 148, 8);}
}

@-webkit-keyframes cursor_blink {
    0% {background-color: rgb(253, 148, 8);}
    50% {background-color: rgb(20, 23, 27);
        color: white;}
    100% {background-color: rgb(253, 148, 8);}
}


