nav {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 0;
    overflow-x: hidden;
    background-color: lightblue;
    z-index: 100;
    -webkit-transition: ease-out 0.5s;
    -o-transition: ease-out 0.5s;
    transition: ease-out 0.5s;
}

nav.open {
    width: 300px;
}

.navigation-toggle {
    position: fixed;
    margin-left: 0;
    -webkit-transition: ease-out 0.5s;
    -o-transition: ease-out 0.5s;
    transition: ease-out 0.5s;
}

.navigation-toggle.active {
    margin-left: 300px;
}

.nav-button {
    border: 1px solid black;
}

.nav-button.active {
    color: red;
}


